.html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-color: #eee;
  word-break: break-word;
}
.form {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 20px;
}

@media (max-width:767px) {
    .form {
          grid-template-columns: 1fr;
    }
    
}

.form__input {
    border: 1px solid #ddd;
    padding: 10px;
}

.form__button {
    background-color: teal;
    color: #ffffff;
    border: none;
    padding: 10px;
    transition: 0.4s;
    cursor: pointer;
}

.form__button:hover {
    background-color: hsl(180, 100%, 35%);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.form__button:active {
    background-color: hsl(180, 100%, 40%);
}


.tasks {
    padding: 0px;
    margin: 0px;
    list-style: none;
}

.tasks__content {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-gap: 10px;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #ddd;
}

.tasks__content--hidden {
    display: none;
}

.tasks__done {
    text-decoration: line-through;
}

.tasks__added {
    flex-grow: 1;
    text-align: left;
    word-break: break-word;
    padding-left: 5px;
}

.tasks__button {
    border: none;
    color: white;
    width: 30px;
    height: 30px;
    padding: 0px;
    transition: 0.4s;
    cursor: pointer;
}

.tasks__button--toggleDone {
    background: hsl(122, 65%, 29%);
}

.tasks__button--toggleDone:hover {
    background: hsl(122, 65%, 35%);
}

.tasks__button--toggleDone:active {
    background: hsl(122, 65%, 40%);
}

.tasks__button--remove {
    background: hsl(354, 84%, 54%);
}

.tasks__button--remove:hover {
    background: hsl(354, 84%, 60%);
}

.tasks__button--remove:active {
    background: hsl(354, 84%, 65%);
}


.buttons {
    display: flex;
    flex-wrap: wrap;
}

.buttons__button {
    border: none;
    color: teal;
    background-color: transparent;
    transition: color 0.3s;
    margin: 0 0 0 20px;
    cursor: pointer;
}

@media (max-width: 767px) {
    .buttons__button {
        flex-basis: 100%;
        margin: 10px;
    }

}

.buttons__button:hover {
    color: hsl(180, 100%, 35%);
}

.buttons__button:active {
    color: hsl(180, 100%, 40%);
}

.buttons__button:disabled {
    color: #ccc;
}
.section {
    margin: 10px 0;
    background: white;
    box-shadow: 0 0 5px #ddd;
}

.section__header {
    border-bottom: 1px solid #ddd;
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 20px;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

@media(max-width: 767px) {
    .section__header {
        grid-template-columns: 1fr;
    }
}

.section__body {
    padding: 20px;
}
.container {
    max-width: 900px;
    padding: 20px;
    margin: 0 auto;
}
