.buttons {
    display: flex;
    flex-wrap: wrap;
}

.buttons__button {
    border: none;
    color: teal;
    background-color: transparent;
    transition: color 0.3s;
    margin: 0 0 0 20px;
    cursor: pointer;
}

@media (max-width: 767px) {
    .buttons__button {
        flex-basis: 100%;
        margin: 10px;
    }

}

.buttons__button:hover {
    color: hsl(180, 100%, 35%);
}

.buttons__button:active {
    color: hsl(180, 100%, 40%);
}

.buttons__button:disabled {
    color: #ccc;
}